<template>
  <div class="w-100 text-center mt-3">
    <div class="mb-3">
      <button 
        @click="$store.dispatch('resetDatabase')"
        class="btn btn-dark">Reset Database</button>
    </div>
    <h3>Color themes</h3>
    <h4>https://bootswatch.com/</h4>
    <div class="w-75 m-auto">
      <button
        @click="$store.commit('setCurrentTheme', themeItem)"
        :class="'font-weight-bold btn btn-block ' + themeItem"
        v-for="themeItem in theme.list"
        :key="themeItem"
      >{{ themeItem }} {{ themeItem === theme.current ? "current" : "" }}
      </button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters({
      theme: 'theme',
    }),
  },
}
</script>
